import { html } from 'lit';
import { property } from 'lit/decorators.js';
import { OmniElement } from '../../omni-element.js';
import { styleMap } from 'lit/directives/style-map.js';
import { when } from 'lit/directives/when.js';

import {css} from 'lit';
const styles = css`:host{position:relative}div{display:flex;background-color:#e6ebf1;border-radius:4px}div.show-percent{margin-right:30px}div .bar{background-image:linear-gradient(90deg, rgb(1, 161, 210) 10%, rgb(3, 187, 243) 49%, rgb(1, 161, 210) 90%);height:12px;border-radius:4px;transition:all 300ms ease-out}span.percent{position:absolute;right:0;top:-3px;font-size:11px;font-weight:normal;font-stretch:normal;font-style:normal;line-height:normal;letter-spacing:.24px;text-align:right;color:#a2a9ad}`


/**
 * Visually track task progress from initiation to completion
 *
 * To include within your project `import { OmniProgressBar } from 'omni-ui';`
 *
 * ```html
 * <omni-progress-bar .value=${50}></omni-progres-bar>
 * ```
 *
 * @tagname omni-progress-bar
 */
export class OmniProgressBar extends OmniElement {
  static get styles() {
    return [...super.styles, styles];
  }

  /** value between 0 - 100 for current progress in bar */
  @property({ type: Number }) value: number = 0;
  /** display a percentage on the right edge of the progress bar */
  @property({ type: Boolean, attribute: true, reflect: true }) percent: boolean = false;

  render() {
    return html`
      <div class=${this.percent ? 'show-percent' : ''}>
        <div class="bar" style=${styleMap({ width: `${this.value}%` })}></div>
      </div>
      ${when(this.percent, () => html` <span class="percent">${this.value}%</span> `)}
    `;
  }
}

OmniElement.register('omni-progress-bar', OmniProgressBar);

declare global {
  interface HTMLElementTagNameMap {
    'omni-progress-bar': OmniProgressBar;
  }
}
